


.App {
  text-align: center;
}


.App-header {
  background-color: #161A30;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.movie-container {
  width: 95%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 15px;
 margin-bottom: 35px;
}

.movie-wrapper {
  width: 225px;
  background-color: #F0ECE5;
  color: #31304D;
  border-radius: 10px;
}
.movie-search {
  font-size: 24px;
  margin-bottom: 10px;
  padding: 15px;
  font-weight: bold;
  width: 450px;
}
.movie-title {
  height: 70px;
  margin-top: 20px;
  font-weight: bold;
}
.movie-date {
  font-style: italic;
  margin-top: 15px;
}
.movie-rate {
  color: red;
  margin-bottom: 24px;
  font-weight: bold;
  font-size: 1.2em;
  text-decoration: underline;
}

.movie-image {
  width: 170px;
  border-radius: 10px;
  box-shadow: 0px 3px 11px 0px rgba(0,0,0,0.61);
-webkit-box-shadow: 0px 3px 11px 0px rgba(0,0,0,0.61);
-moz-box-shadow: 0px 3px 11px 0px rgba(0,0,0,0.61);
}
footer {
  background-color: #31304D;
  width: 100%;
  color: #F0ECE5;
  font-style: italic;
  font-size: 0.8em;
  padding-bottom: 10px;
}
@media screen and (max-width: 600px) {
  .movie-container {
    width: 98%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    gap: 15px;
    margin-bottom: 35px;
  
  }
  
  .movie-wrapper {
    width: 165px;
    background-color: #F0ECE5;
    color: #31304D;
    border-radius: 10px;
  }
  .movie-search {
    font-size: 18px;
    margin-bottom: 10px;
    padding: 15px;
    font-weight: bold;
    width: 275px;
  }
  .movie-title {
    height: 70px;
    margin-top: 20px;
    font-weight: bold;
  }
  .movie-date {
    font-style: italic;
    margin-top: 15px;
  }
  .movie-rate {
    color: red;
    margin-bottom: 24px;
    font-weight: bold;
    font-size: 1.2em;
    text-decoration: underline;
  }
  
  .movie-image {
    width: 150px;
    border-radius: 10px;
    box-shadow: 0px 3px 11px 0px rgba(0,0,0,0.61);
  -webkit-box-shadow: 0px 3px 11px 0px rgba(0,0,0,0.61);
  -moz-box-shadow: 0px 3px 11px 0px rgba(0,0,0,0.61);
  }
  }